header {
    align-items: center;
    background-color: #30343F;
    display: flex;
    height: 144px;  /*-----*/
    justify-content: center;
    left: 0px;
    position: relative;
    top: 0px;
    width: 100%;
}

header > div {
    display: flex;
    flex-direction: row;
    height: 65px;   /*-----*/
    justify-content: space-between;
    margin: auto;
    width: 84.68%;  /*-----*/
}

header > div > div {
    align-items: center;
    justify-content: space-between;
    display: flex;
    width: 372px;   /*-----*/
}

.headerLogo {
    width: 80.24px;     /*-----*/
    height: 65.19px;    /*-----*/
}

.headerTitle {
    position: relative;
    width: 270.92px;     /*-----*/
    height: 30.31px;    /*-----*/
}

ul {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 406px;  /*-----*/
}

li {
    list-style: none;
}

li > button {
    background-color: #30343F;
    border: none;
    color: #FFFFFF;
    font-family: 'Raleway', sans-serif;
    font-size: 20px;  /*-----*/
    font-style: normal;
    font-weight: 500;  /*-----*/
    line-height: 23px;  /*-----*/
    padding: 0;
    text-transform: uppercase;
}

.shopArrowDown {
    height: 11.59px;  /*-----*/
    left: 5.66px;  /*-----*/
    position: relative;
    width: 14px;  /*-----*/
}

.cartIcon, .cartIconWrapper {
    height: 31px;  /*-----*/
    position: relative;
    width: 33px;  /*-----*/
}

.ellipseIconWrapper {
    height: 18px;  /*-----*/
    position: absolute;
    right: -9px;  /*-----*/    /*50% of width/height*/
    top: -4.5px;  /*-----*/    /*25% of width/height*/
    width: 18px;  /*-----*/
    z-index: 1;
}

.ellipseIcon {
    height: 18px;  /*-----*/
    width: 18px;  /*-----*/
}

.ellipseIconNumber {
    color: #FFFFFF;
    font-family: 'Inter', sans-serif;
    font-size: 9px;  /*-----*/
    font-style: normal;
    font-weight: 700;  /*-----*/
    height: 18px;  /*-----*/
    line-height: 18px;  /*-----*/  /*100% of height*/
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    width: 18px;  /*-----*/
}
