@media screen and (min-width: 1025px) and  (max-width: 1280px) {
    header {
        width: 100%;
    }

    ul {
        width: 305.62px;
    }

    li > button {
        font-size: 18px;
        font-weight: 400;
    }
}

@media screen and (max-width: 1024px) {
    header {
        padding-top: 39px;
        padding-bottom: 39px;
    }

    header > div {
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        margin: 0;
        height: 100%;
    }

    ul {
        width: 288.7px;
    }

    li > button {
        font-size: 14px;
        font-weight: 400;
    }
}

@media screen and (max-width: 768px) {
    header {
        height: auto;
    }

    header > div {
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        height: 183px;
    }

    header > div > div {
        flex-direction: column;
    }

    .headerTitle {
        margin-top: 20.84px;
    }

    ul {
        width: 288.7px;
    }

    li > button {
        font-size: 14px;
        font-weight: 400;
    }
}

@media screen and (max-width: 480px) {
    header {
        height: auto;
    }

    header > div {
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        height: 298.32px;
    }

    header > div > div {
        flex-direction: column;
    }

    .headerTitle {
        margin-top: 20.84px;
    }

    ul {
        flex-direction: column;
        width: 288.7px;
        height: 143px;
    }

    li > button {
        font-size: 14px;
        font-weight: 400;
    }
}